import React from 'react';
import './Card.css';
import { useSpring, animated, interpolate } from 'react-spring';
import { useDrag } from 'react-use-gesture';

const SwipeCard = (props) => {
	const { children } = props;
	const [{ x }, set] = useSpring(() => ({ x: 0 }))
	const cardRef = React.createRef(null);
	const bind = useDrag(({ down, delta ,movement: [mx, _] }) => {
		const widthCard = cardRef.current.offsetWidth;
		const halfWidth = Math.floor(widthCard / 2);
		if (mx > halfWidth) {
			console.log('isRight');
			set({
				x: down ? mx : widthCard + 100,
			})
		} else if (mx < -halfWidth) {
			console.log('isLeft');
			set({
				x: down ? mx : -widthCard - 100,
			})
		} else {
			console.log('isDragable');
			set({
				x: down ? mx : 0,
			})
		}
		
	})

	return (
		<animated.div ref={cardRef} className={'card-question'} {...bind()} style={{ x }} >
			{children}
		</animated.div>
	)
};

const Card = (props) => {
	return (
		<div className='card-question__inner'>
			<div className='card-question__text_top card-question__text'>You can completely remake your body into exactly how you want to look and nothing you do will change it (lack of exercise, bad eating, etc.)</div>
			<div className='card-question__text_middle card-question__text'>НО</div>
			<div className='card-question__text_bottom card-question__text'>If anyone finds out that you remade yourself all the changes go away</div>
		</div>
	);
}

const CardQuestion = (props) => {

	return (
		<SwipeCard>
			<Card {...props} />
		</SwipeCard>
	)
};

export default CardQuestion;